<template>
    <div class="absolute top-1 right-1">
        <button @click="localSwitcher('fr')" type="button" class="p-2 border w-10 h-10 m-1 bg-black text-white">FR</button>
        <button @click="localSwitcher('en')" type="button" class="p-2 border w-10 h-10 m-1 bg-black text-white">EN</button>
    </div>
</template>

<script>
export default {
    name:"LocalSwitcher",
    methods:{
        localSwitcher(lang){
            this.$i18n.locale = lang;
        }
    }
}
</script>

