<script>
import { useI18n } from "vue-i18n";
import LocalSwitcher from "../components/LocalSwitcher.vue";

  export default {
    name: "Home",
    components: {
      LocalSwitcher
    },
    setup() {
      const { t } = useI18n({
        inheritLocale: true,
        useScope: "local",
      });
      return { t };
    },
    methods:{
      scrollingEffect() {
        var elements;
        var windowHeight;

        function init() {
          elements = document.querySelectorAll(".opacity-0");
          windowHeight = window.innerHeight;
        }

        function checkPosition() {
          for (var i = 0; i < elements.length; i++) {
            var element = elements[i];
            var positionFromTop = elements[i].getBoundingClientRect().top;

            if (positionFromTop - windowHeight <= -180) {
              element.classList.add("transition_top");
              element.classList.remove("opacity-0");
            }
          }
        }
        init();
        checkPosition();
        window.addEventListener("scroll", checkPosition);
        window.addEventListener("scroll", init);
      },
    },
    beforeMount(){
      this.scrollingEffect();
    },
  }
</script>

<style lang="scss">
.home {
  font-family: 'Roboto', sans-serif;
}
.font-playfair {
  font-family: 'Playfair Display', sans-serif;
}
.bg-highfive {
  background: url("../assets/highfive.jpg");
  background-position: center;
  background-repeat: repeat-x;
  background-attachment: fixed;
}
.transition_top {
  animation: transitiontopY 1s ease-out 0s both;
}
@keyframes transitiontopY {
  0% {
    transform: translateY(2rem);
    opacity: 0;
  }
  25% {
    opacity: 0.2;
  }
  50% {
    opacity: 0.4;
  }
  80% {
    opacity: 0.6;
  }
  90% {
    opacity: 0.9;
  }
  100% {
    transform: translateY(0rem);
    opacity: 1;
  }
}
</style>
